import React, { createContext, memo, useState } from 'react';
import { Query } from 'react-apollo';
import gql from 'graphql-tag';
import debounce from 'lodash.debounce';

export const AlumniFetcherContext = createContext();

const AlumniFetcher = ({ firstTime, children }) => {
  const [activeScreen, setActiveScreen] = useState(
    firstTime ? 'setup' : undefined
  );
  const [searchValue, setSearchValue] = useState('');
  const [searchQuery, setSearchQuery] = useState('');
  const [activeAlum, setActiveAlum] = useState(undefined);
  const [locationVariables, setLocationVariables] = useState({
    id: 0,
    level: 0,
  });
  const [activeFilterSet, setActiveFilterSet] = useState(undefined);
  const [activeFilters, setActiveFilters] = useState({
    majors: [],
    graduationYearFrom: null,
    graduationYearTo: null,
    company: '',
    jobTitle: '',
    industries: [],
  });
  const [filtered, setFilterered] = useState(false);
  const [flow, setFlow] = useState('forward');

  const handleSearch = debounce(searchQuery => {
    setSearchQuery(searchQuery);
  }, 500);

  const resetSearch = () => {
    setSearchValue('');
    setSearchQuery('');
  };

  const handleSearchInputChange = event => {
    const {
      target: { value },
    } = event;
    setSearchValue(value);
    handleSearch(value);
  };

  const fetchLocation = ({ level, id }) => {
    setFlow(level >= locationVariables.level ? 'forward' : 'backward');
    setLocationVariables({ level: +level, id: +id });
  };

  const handleSetActiveFilters = activeFilters => {
    const {
      majors,
      graduationYearFrom,
      graduationYearTo,
      company,
      jobTitle,
      industries,
    } = activeFilters;
    const filtered = !(
      majors.length === 0 &&
      graduationYearFrom === null &&
      graduationYearTo === null &&
      company === '' &&
      jobTitle === '' &&
      industries.length === 0
    );
    setActiveFilters(activeFilters);
    setFilterered(filtered);
  };

  const resetActiveFilters = () => {
    setActiveFilters({
      majors: [],
      graduationYearFrom: null,
      graduationYearTo: null,
      company: '',
      jobTitle: '',
      industries: [],
    });
  };

  const resetLocation = () => {
    fetchLocation({ level: 0, id: undefined });
  };

  return (
    <Query
      query={GET_ACTIVE_LOCATION}
      variables={locationVariables}
      fetchPolicy="cache-and-network"
    >
      {({ data, loading: activeLocationLoading }) => (
        <Query
          query={GET_USERS}
          variables={{
            level: +locationVariables.level,
            locationId: +locationVariables.id,
            searchValue: searchQuery,
            activeFilters,
          }}
          fetchPolicy="cache-and-network"
        >
          {({ data: { users }, loading: alumniLoading }) => (
            <AlumniFetcherContext.Provider
              value={{
                searchValue,
                activeScreen,
                activeAlum,
                filtered,
                flow,
                activeFilters,
                activeFilterSet,
                loading: alumniLoading || activeLocationLoading,
                locations: data.activeLocation
                  ? data.activeLocation.locations
                  : [],
                activeLocation: {
                  level: locationVariables.level,
                  id: locationVariables.id,
                  parent: data.activeLocation
                    ? data.activeLocation.parent
                    : undefined,
                  name: data.activeLocation
                    ? data.activeLocation.name
                    : undefined,
                },
                setActiveScreen,
                setActiveAlum,
                numAlumni: users && users.length,
                alumni: users,
                fetchLocation,
                handleSearchInputChange,
                resetSearch,
                setActiveFilterSet,
                setActiveFilters: handleSetActiveFilters,
                resetActiveFilters,
                resetLocation,
              }}
            >
              {children}
            </AlumniFetcherContext.Provider>
          )}
        </Query>
      )}
    </Query>
  );
};

export default memo(AlumniFetcher);

const GET_USERS = gql`
  query Users(
    $level: Int
    $locationId: Int
    $searchValue: String
    $activeFilters: Filters
  ) {
    users(
      level: $level
      locationId: $locationId
      searchValue: $searchValue
      activeFilters: $activeFilters
    ) {
      id
      email
      firstName
      lastName
      birthDate
      graduationYear
      major
      streetAddress1
      streetAddress2
      city
      state
      postalCode
      currentCompany
      currentJobTitle
      currentIndustry
      website
      linkedIn
      instagram
      facebook
      twitter
      profileImage
      bio
      cityId
      stateId
      countryId
      continentId
      graduationYearId
      majorId
      industryId
      visibleFields
      accepted
      firstTime
    }
  }
`;

const GET_ACTIVE_LOCATION = gql`
  query ActiveLocation($level: Int!, $id: Int) {
    activeLocation(level: $level, id: $id) {
      id
      name
      locations {
        id
        name
      }
      parent {
        id
        name
      }
    }
  }
`;
