import React, { memo, useContext, useEffect, useRef } from 'react';
import {
  disableBodyScroll,
  enableBodyScroll,
  clearAllBodyScrollLocks,
} from 'body-scroll-lock';
import styled from 'styled-components';
import { Scrollbars } from 'react-custom-scrollbars';
import CloseButton from '../shared/CloseButton';
import { colors, fontFamilies, viewports } from '../../utils/variables';
import HorizontalSlider from '../shared/HorizontalSlider';
import ExploreSidebarNav from './ExploreSidebarNav';
import { AlumniFetcherContext } from './AlumniFetcher';
import LoadingIcon from '../shared/LoadingIcon';

const ExploreSidebar = ({ visible, toggleSidebar }) => {
  const { fetchLocation, activeLocation, locations, loading } = useContext(
    AlumniFetcherContext
  );

  const sidebarRef = useRef(null);

  useEffect(() => {
    if (visible) {
      disableBodyScroll(sidebarRef.current);
    } else {
      enableBodyScroll(sidebarRef.current);
    }
    return () => clearAllBodyScrollLocks();
  });

  return (
    <Base start={-100} visible={visible} ref={sidebarRef}>
      <ExploreSidebarNav
        activeLocation={activeLocation}
        fetchLocation={fetchLocation}
      />
      <CloseLink onClick={() => toggleSidebar(undefined)} />
      <Scrollbars autoHide>
        <LocationsWrapper>
          {loading ? (
            <LoadingIcon />
          ) : (
            <Locations>
              {locations
                .sort((a, b) => a.name > b.name)
                .map(location => (
                  <Location
                    key={location.id}
                    inactive={
                      +activeLocation.level === 4 &&
                      +activeLocation.id === +location.id
                    }
                    onClick={() => {
                      fetchLocation({
                        level:
                          activeLocation.level !== 4
                            ? activeLocation.level + 1
                            : activeLocation.level,
                        id: +location.id,
                      });
                      if (
                        activeLocation.level === 3 ||
                        activeLocation.level === 4
                      )
                        toggleSidebar(undefined);
                    }}
                  >
                    {location.name}
                  </Location>
                ))}
            </Locations>
          )}
        </LocationsWrapper>
      </Scrollbars>
    </Base>
  );
};

export default memo(ExploreSidebar);

const Base = styled(HorizontalSlider)`
  background-color: ${colors.red};
  bottom: 0;
  display: flex;
  flex-direction: column;
  height: 100vh;
  left: 0;
  padding: 1.6875rem 1.875rem;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 10;

  @media ${viewports.medium} {
    position: absolute;
    width: 20rem;
  }
`;

const CloseLink = styled(CloseButton)`
  fill: #fffff7;
  position: absolute;
  right: 1.9375rem;
  top: 1.9375rem;

  @media ${viewports.medium} {
    &:hover {
      fill: #ffd1b7;
    }
  }
`;

const LocationsWrapper = styled.div`
  flex: 1;
  margin-top: 1.875rem;
  position: relative;

  &:after {
    background: linear-gradient(
      to bottom,
      rgba(252, 70, 0, 0) 0%,
      rgba(252, 70, 0, 1) 100%
    );
    bottom: 0;
    content: '';
    height: 6.25rem;
    left: 0;
    position: fixed;
    right: 0;
  }
`;

const Locations = styled.div`
  padding-bottom: 3.25rem;
`;

const Location = styled.a`
  color: ${props => (props.inactive ? '#ffd1b7' : '#fffff7')};
  cursor: pointer;
  display: block;
  font-family: ${fontFamilies.bold};
  font-size: 2rem;
  margin-bottom: 0.75rem;
  pointer-events: ${props => (props.inactive ? 'none' : 'auto')};

  @media ${viewports.medium} {
    &:hover {
      color: #ffd1b7;
    }
  }
`;
