import uniqBy from 'lodash.uniqby';
import sortBy from 'lodash.sortby';

const orderInfographicStats = ar => {
  const total = ar.length;
  const newArray = ar.map(item => ({
    name: item,
    count: ar.filter(_ => _ === item).length,
  }));
  const uniqueArray = uniqBy(newArray, _ => _.name);
  const sortedArray = sortBy(uniqueArray, _ => _.count).reverse();
  return sortedArray
    .map(_ => ({
      name: _.name,
      percent: parseFloat(((_.count / total) * 100).toFixed(2)),
    }))
    .filter((_, i) => i < 3);
};

const infographicStats = alumni => {
  const majors = orderInfographicStats(alumni.map(_ => _.major));
  const classYears = orderInfographicStats(alumni.map(_ => _.graduationYear));
  const industries = orderInfographicStats(alumni.map(_ => _.currentIndustry));
  return {
    majors,
    classYears,
    industries,
  };
};

export default infographicStats;
