import React, { memo } from 'react';
import SectionHeading from './SectionHeading';
import Infographic from './Infographic';

const LocationWrapper = ({ activeLocation, numAlumni, loading, alumni }) => (
  <>
    <SectionHeading
      sectionName={activeLocation.name}
      numAlumni={numAlumni}
      loading={loading}
    />
    {!loading && numAlumni > 0 && (
      <Infographic activeLocation={activeLocation} alumni={alumni} />
    )}
  </>
);

export default memo(LocationWrapper);
