import React, { memo, lazy, Suspense, useState } from 'react';
import { Query } from 'react-apollo';
import gql from 'graphql-tag';
import Layout from '../components/shared/Layout';
import AlumniFetcher, {
  AlumniFetcherContext,
} from '../components/explore/AlumniFetcher';
import AlumProfile from '../components/alum-profile/AlumProfile';
import ExploreLayout from '../components/explore/ExploreLayout';
import PageLoader from '../components/shared/PageLoader';
import { userFromLocalCookie } from '../utils/auth';

const ProfileSetup = lazy(() =>
  import('../components/profile-setup/ProfileSetup')
);
const ProfileSettings = lazy(() =>
  import('../components/profile-settings/ProfileSettings')
);

const Tour = lazy(() => import('../components/tour/Tour'));

const ExplorePage = () => {
  const [tourScreenIndex, setTourScreenIndex] = useState(0);

  return (
    <Layout title="ArtCenter - Explore" protectedRoute redirect="/">
      <Query query={GET_USER} variables={{ id: +userFromLocalCookie() }}>
        {({ data: { user }, loading, refetch }) =>
          !loading && (
            <AlumniFetcher firstTime={user.firstTime} user={user}>
              <AlumniFetcherContext.Consumer>
                {({
                  setActiveScreen,
                  setActiveAlum,
                  activeScreen,
                  activeAlum,
                }) => (
                  <>
                    <ExploreLayout tourScreenIndex={tourScreenIndex} />
                    <Suspense fallback={<PageLoader />}>
                      {activeScreen === 'setup' && (
                        <ProfileSetup
                          setActiveScreen={setActiveScreen}
                          id={user.id}
                          refetch={refetch}
                        />
                      )}
                    </Suspense>
                    {activeScreen === 'alum' && (
                      <AlumProfile
                        alum={activeAlum}
                        setActiveScreen={setActiveScreen}
                        setActiveAlum={setActiveAlum}
                      />
                    )}
                    <Suspense fallback={<PageLoader />}>
                      {(activeScreen === 'profile-settings' ||
                        activeScreen === 'account-settings') && (
                        <ProfileSettings
                          setActiveScreen={setActiveScreen}
                          activeScreen={activeScreen}
                          user={user}
                        />
                      )}
                    </Suspense>
                    <Suspense fallback={<PageLoader />}>
                      {activeScreen === 'tour' && (
                        <Tour
                          setActiveScreen={setActiveScreen}
                          tourScreenIndex={tourScreenIndex}
                          setTourScreenIndex={setTourScreenIndex}
                        />
                      )}
                    </Suspense>
                  </>
                )}
              </AlumniFetcherContext.Consumer>
            </AlumniFetcher>
          )
        }
      </Query>
      <Query query={GET_PAGE_LOADING}>
        {({ data: { pageLoading } }) => pageLoading && <PageLoader />}
      </Query>
    </Layout>
  );
};

export default memo(ExplorePage);

const GET_USER = gql`
  query User($id: Int) {
    user(id: $id) {
      id
      email
      firstName
      lastName
      birthDate
      graduationYear
      major
      streetAddress1
      streetAddress2
      city
      state
      postalCode
      currentCompany
      currentJobTitle
      currentIndustry
      website
      linkedIn
      instagram
      facebook
      twitter
      profileImage
      bio
      cityId
      stateId
      countryId
      continentId
      graduationYearId
      majorId
      industryId
      visibleFields
      accepted
      firstTime
    }
  }
`;

const GET_PAGE_LOADING = gql`
  {
    pageLoading @client
  }
`;
