import React, { memo } from 'react';
import styled from 'styled-components';
import RightNavArrow from '../icons/RightNavArrow';
import { fontFamilies, viewports } from '../../utils/variables';

const ExploreSidebarNav = ({ activeLocation, fetchLocation }) => {
  const { level, parent } = activeLocation;
  return {
    0: <Title>Explore</Title>,
    1: (
      <BackLink
        onClick={() =>
          fetchLocation({
            level: 0,
            id: undefined,
          })
        }
      >
        <BackIcon />
        <Back>World</Back>
      </BackLink>
    ),
    2: (
      <BackLink
        onClick={() =>
          fetchLocation({
            level: 1,
            id: +parent.id,
          })
        }
      >
        <BackIcon />
        {parent && <Back>{parent.name}</Back>}
      </BackLink>
    ),
    3: (
      <BackLink
        onClick={() =>
          fetchLocation({
            level: 2,
            id: +parent.id,
          })
        }
      >
        <BackIcon />
        {parent && <Back>{parent.name}</Back>}
      </BackLink>
    ),
    4: (
      <BackLink
        onClick={() =>
          fetchLocation({
            level: 2,
            id: +parent.id,
          })
        }
      >
        <BackIcon />
        {parent && <Back>{parent.name}</Back>}
      </BackLink>
    ),
  }[level];
};

export default memo(ExploreSidebarNav);

const Title = styled.h4`
  color: #ffffff;
  font-family: ${fontFamilies.bold};
  font-size: 1.25rem;
`;

const BackLink = styled.a`
  cursor: pointer;
  width: 80%;
`;

const Back = styled.span`
  color: #ffffff;
  font-size: 1.25rem;
  padding-left: 1rem;

  @media ${viewports.medium} {
    ${BackLink}:hover & {
      color: #ffd1b7;
    }
  }
`;

const BackIcon = styled(RightNavArrow)`
  fill: #fff;
  transform: rotate(180deg);

  @media ${viewports.medium} {
    ${BackLink}:hover & {
      fill: #ffd1b7;
    }
  }
`;
