import React, { memo } from 'react';
import { Transition, animated } from 'react-spring';

const GridTransitionContainer = ({
  children,
  condition,
  flow,
  ignoreScale,
}) => (
  <Transition
    native
    items={condition}
    from={{
      s: flow === 'forward' ? 0 : 1.5,
      o: 0,
    }}
    enter={{ s: 1, o: 1 }}
    leave={{
      s: flow === 'forward' ? 1.5 : 0,
      o: 0,
    }}
  >
    {visible =>
      visible &&
      (({ s, o }) => (
        <animated.div
          style={{
            bottom: 0,
            left: 0,
            position: 'fixed',
            right: 0,
            top: 0,
            transform: ignoreScale ? 1 : s.interpolate(s => `scale(${s})`),
            opacity: o.interpolate(o => o),
          }}
        >
          {children}
        </animated.div>
      ))
    }
  </Transition>
);

export default memo(GridTransitionContainer);
