import React, { memo } from 'react';
import styled from 'styled-components';
import { Transition, animated } from 'react-spring';
import { fontFamilies, viewports } from '../../utils/variables';

const SectionHeading = ({
  isWorld,
  sectionName,
  numAlumni,
  loading,
  className,
}) => (
  <Base className={className}>
    {isWorld ? (
      <TitleContainer>
        <Title>{sectionName}</Title>
      </TitleContainer>
    ) : (
      <TitleContainer>
        <Transition
          native
          items={!loading}
          from={{ o: 0, y: 2 }}
          enter={{ o: 1, y: 0 }}
          leave={{ o: 0, y: -2 }}
          config={{ delay: !loading ? 600 : 0 }}
          immediate={loading}
        >
          {show =>
            show &&
            (({ o, y }) => (
              <animated.div
                style={{
                  opacity: o.interpolate(opacity => opacity),
                  transform: y.interpolate(y => `translateY(${y}rem)`),
                }}
              >
                <Title>{sectionName}</Title>
              </animated.div>
            ))
          }
        </Transition>
      </TitleContainer>
    )}
    <AlumniCountContainer>
      <Transition
        native
        items={!loading}
        from={{ o: 0, y: 2 }}
        enter={{ o: 1, y: 0 }}
        leave={{ o: 0, y: -2 }}
        config={{ delay: !isWorld && !loading ? 1200 : 0 }}
        immediate={loading}
      >
        {show =>
          show &&
          (({ o, y }) => (
            <animated.div
              style={{
                opacity: o.interpolate(o => o),
                transform: y.interpolate(y => `translateY(${y}rem)`),
              }}
            >
              <AlumniCount>{numAlumni} Alumni</AlumniCount>
            </animated.div>
          ))
        }
      </Transition>
    </AlumniCountContainer>
  </Base>
);

export default memo(SectionHeading);

const Base = styled.div`
  margin-top: 5rem;
  text-align: center;
  width: 100%;

  @media ${viewports.medium} {
    left: 10rem;
    margin-top: 0;
    top: 5.25rem;
    position: absolute;
    text-align: left;
    width: 50%;
  }
`;

const TitleContainer = styled.div`
  height: 4rem;
  left: 0;
  overflow: hidden;
  top: 0;

  @media ${viewports.medium} {
    position: absolute;
    width: 34.375rem;
  }
`;

const Title = styled.h3`
  font-size: 2.25rem;
  font-family: ${fontFamilies.bold};
  letter-spacing: -0.5px;
  line-height: 58px;

  @media ${viewports.medium} {
    font-size: 3.5rem;
  }
`;

const AlumniCountContainer = styled.div`
  height: 3rem;
  overflow: hidden;
  top: 4.125rem;
  @media ${viewports.medium} {
    position: absolute;
  }
`;

const AlumniCount = styled.h4`
  color: #adada8;
  display: flex;
  font-size: 1.5rem;
  justify-content: center;
  line-height: 111%;

  @media ${viewports.medium} {
    font-size: 2.25rem;
  }
`;
